.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: #212123 ;
    
    
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    color: aliceblue;
}
.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.location{
    display: flex;
    margin-bottom: 0.8rem;
}
.location p{
    line-height: 30px;
    color: #ffff;
}


.right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    align-items: center;
}
.social{
    margin-top: 1rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
    color: #ffff;
}


.right p{
    padding-bottom: 0.7rem;
    color: #ffff;
    line-height: 30px;
    text-align: center;
}

@media  screen and (max-width: 640px) {
    .footer-container{
        grid-template-columns: 1fr;
    }    
}