.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}

.intro-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
    opacity: 0.9;
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgb(18, 19, 17);
    opacity: 0.45;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;

}

.hero .content h1{
    font-size: 4rem;
    padding: 0.8rem 0 1.5rem;
    color: aliceblue;
}
.hero .content p{
    font-size: 1.4rem;
    font-weight: 400;
    text-transform: uppercase;
    color: aliceblue;
    padding-bottom: 2.9rem;
}

@media screen and (max-width: 640px) {
    .hero .content h1{
        font-size: 3rem;
    }

    .hero .content p{
        font-size: 1.4rem;
        font-weight: 400;
        text-transform: uppercase;
        color: aliceblue;
        padding-bottom: 2.9rem;
    }

    .btn, .btn-light{
        padding: 8px 8px;
        font-size: 0.7rem;
        cursor: pointer;
        transition: 0.3s;
       
    }


}